import { ChannelParam, NoteStatus } from "../redux/store/backingState";


export class BackingUtil {

    // 
    static OCTAVE_NUM = 8;
    // 
    static STRUCT_NUM = 6;
    // 
    static CHANNEL_MAX = 12;

    static getInitialRecord = (divCount: number) => {
        return new Array<NoteStatus | null>(divCount).fill(null);
    }

    static getVoicingStructs = (table: boolean[][]): ChannelParam[] => {
        const channels: ChannelParam[] = [];
        for (let i = 0; i < table.length; i++) {
            for (let j = 0; j < table[i].length; j++) {
                if (table[i][j]) {
                    channels.push({ octave: i, structIndex: j });
                }
            }
        }
        if (channels.length === 0) {
            channels.push({ octave: -1, structIndex: 0 });
        }
        return channels;
    }

    static getChannels = (voicingStructs: ChannelParam[], size: number) => {
        const channels: ChannelParam[] = [];
        for (let i = 0; i < size; i++) {
            const channel: ChannelParam = { octave: -1, structIndex: 0 };
            if (i < voicingStructs.length) {
                channel.octave = voicingStructs[i].octave;
                channel.structIndex = voicingStructs[i].structIndex;
            }
            channels.push(channel);
        }
        return channels.reverse();
    }
}

export default BackingUtil;