import { useContext, useState } from "react";
import styled from "styled-components";
import { ExecutableButton } from "../../../base/components/button/executableButton";
import { _CSS_LABEL_MIDIUM } from "../../../base/design/styles";
import { ChordBacking, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../redux/store/scoreData";
import { GlobalContext } from "../../entry/systemEntry";
import ScoreBuilder from "./scoreBuilder";

const ManageTab = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    const [source, setSource] = useState<string>('');

    return (
        <_Wrap>
            <_Left>
                <_Link onClick={() => {

                    const data: ScoreData = {
                        chordList: [
                            { type: 'init', detail: { keyIndex: 0, bpm: 100, scale: 'major', timeSignature: '4/4' } as DetailInit },
                            { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                            { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                            {
                                type: 'chord', detail: {
                                    beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
                                    backing: {
                                        voicingList: [],
                                        pattern: null
                                    }
                                } as DetailChord
                            },
                            { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
                        ],
                        notesList: []
                    };
                    setSource(JSON.stringify(data));
                }} >Create an empty score.</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', timeSignature: '4/4' });
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 2);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 1);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    setSource(JSON.stringify(builder.getScoreData()));
                }} >mm</_Link>
            </_Left>
            <_Right>
                <_Source value={source} onChange={e => setSource(e.target.value)} />
                <_ButtonsDiv>
                    <_Button onClick={() => {
                        setSource(JSON.stringify(store.scoreData));
                    }}>Reload</_Button>
                    <_Button onClick={() => {
                        const data = JSON.parse(source);
                        dispatcher.score.setScoreData(data);
                        store.scoreState.focusIndex = 2;
                        dispatcher.score.setScoreState({ ...store.scoreState });

                        dispatcher.score.setScoreTabIndex(1);
                    }}>Apply</_Button>
                </_ButtonsDiv>
            </_Right>
        </_Wrap>
    );
}

export default ManageTab;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #7e7e7e;
    text-align: left;
`;

const _Left = styled.div`
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #7e7e7e;
    text-align: left;
    vertical-align: top;
`;

const _Right = styled.div`
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #888999;
    text-align: left;
    vertical-align: top;
`;

const _Link = styled.div`
    
    display: inline-block;
    width: calc(100% - 50px);
    height: 50px;
    margin: 20px 25px;
    border: 2px solid #cacaca;
    /* background: linear-gradient(to bottom, #585858, #a7a7a7, #585858); */
    background-color: #000;
    color: #ccc;
    font-size: 30px;
    font-weight: 600;
    text-align: left;
    box-sizing: border-box;
    padding-left: 20px;
    border-radius: 6px;

    &:hover {
        background-color: #616161;
    }
`;

const _Source = styled.textarea`
    display: inline-block;
    width: calc(100% - 8px);
    margin: 4px 0 0 4px;
    height: calc(100% - 44px);
    /* background-color: #7e7e7e; */
    text-align: left;
    font-size: 20px;
    resize: none;
    box-sizing: border-box;
    color: #060053;
`;

const _ButtonsDiv = styled.div`
    display: inline-block;
    width: 100%;
    height: 40px;
    background-color: #5f5f5f;
    box-sizing: border-box;
    text-align: left;
`;

const _Button = styled.div`
    ${_CSS_LABEL_MIDIUM}
    width: 100px;
    background-color: #afafaf;
    border: 1px solid #4d4d4d;
    color: #292929;
    border-radius: 4px;
    margin: 5px 0 0 4px;
    &:hover {
        background-color: #c4c4c4;
    }
`;