import { Dispatcher } from "../../../redux/dispatcher/dispatcher";
import { ChordBacking, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData, VoicingProps } from "../../../redux/store/scoreData";
import { Store } from "../../../redux/store/store";
import { getDefaultBacking } from "../../../utils/instrumentsUtil";
import { DegreeProps, DIATONIC_MAJOR_SCALE_LIST, getSymbolFromKey, SymbolKey } from "../../../utils/theoryUtils";


class ScoreBuilder {

    private scoreData: ScoreData

    private curIndex: number;

    constructor(init: DetailInit) {
        this.scoreData = {
            chordList: [
                { type: 'init', detail: init },
                { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
            ],
            notesList: []
        };

        this.curIndex = 3;
    }

    public addChord = (beatLen: number, root: DegreeProps, symbolKey: SymbolKey) => {

        const symbol = getSymbolFromKey(symbolKey);
        const backing: ChordBacking = symbol.attr == undefined ? { voicingList: [], pattern: null } : getDefaultBacking(symbol.attr);
        this.scoreData.chordList.splice(this.curIndex, 0, {
            type: 'chord', detail: {
                beatLen, minute: { head: 0, tail: 0 }, root, on: null, symbolKey,
                backing
            } as DetailChord
        });
        this.curIndex++;
    }

    public addChordDiatonic = (beatLen: number, scaleIndex: number) => {
        const chord = DIATONIC_MAJOR_SCALE_LIST[scaleIndex];
        this.addChord(beatLen, chord.root, chord.symbolKey);
    }

    public getScoreData = () => { return this.scoreData; }
}

export default ScoreBuilder;