import { useContext } from "react";
import styled from "styled-components";
import { BackingPattern, BackingState, NoteStatus } from "../../../../redux/store/backingState";
import BackingUtil from "../../../../utils/backingUtil";
import { GlobalContext } from "../../../entry/systemEntry";

const ChannelItem = (props: {
    index: number;
    soundFullName: string;
}) => {
    const { store, dispatcher } = useContext(GlobalContext);

    const state = store.backingState as BackingState;
    const pattern = state.pattern as BackingPattern;
    const index = pattern.channels.length - 1 - props.index;

    const update = () => {
        dispatcher.backing.setState(state);
    }
    const addRecord = () => {
        pattern.layers.forEach((layer) => {
            const initialCellArray = BackingUtil.getInitialRecord(layer.noteDivList.length);
            layer.table.splice(index, 0, initialCellArray);
        });
        // alert(pattern.layers[0].table.length);
        const structs = BackingUtil.getVoicingStructs(state.voicingTable as boolean[][]);
        pattern.channels = BackingUtil.getChannels(structs ,pattern.channels.length + 1);
        update();
    }

    const deleteRecord = () => {
        pattern.layers.forEach((layer) => {
            layer.table.splice(index, 1);
        });
        // alert(pattern.layers[0].table.length);
        const structs = BackingUtil.getVoicingStructs(state.voicingTable as boolean[][]);
        pattern.channels = BackingUtil.getChannels(structs ,pattern.channels.length - 1);
        update();
    }

    return (<>
        <_Wrap>
            <_Struct>{props.soundFullName}</_Struct>
            <_Option onClick={deleteRecord}>-</_Option>
            <_Option onClick={addRecord}>+</_Option>
        </_Wrap>
    </>);
}

export default ChannelItem;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 19px;
    background-color: #b4b3b36f;
    margin: 0 0 1px 0;
`;

const _Item = styled.div`
    display: inline-block;
    height: 100%;
    margin: 0 1px 0 0;
    border: 1px solid #322e4292;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 12px;
    font-weight: 600;
    color: #ffffff;
    text-align: center;
`;

const _Struct = styled(_Item)`
    width: 47px;

`;

const _Option = styled(_Item)`
    width: 25px;
    &:hover {
        background-color: #f1ef866c;
    }
    
`;